.header__link {
  color: #A9A9A9;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  text-decoration: none;
  align-self: center;
  transition: opacity .3s;
}

.header__link:hover {
  opacity: .6;
}

@media screen and (max-width: 640px) {
  .header__link {
    font-size: 14px;
    margin-right: 30px;
  }
}
